.banner{
    display: flex;
    width: 90%;
    justify-content: center;
    padding: 0% 2% 0% 2%;
    margin: 2% 5% 0% 5%;
    background-color: whitesmoke;
    border-radius: 10px;
    /* background-color:rgb(229, 204, 231) ; */
    /* border: 1px solid black; */
}
.text-content{
    padding: 0% 2% 0% 2%;
    margin: 8% 2% 0% 2%;
    text-align: left;
    height: 50vh;
    border-radius: 10px;
    background-color: rgba(197, 143, 231, 0.137);
}
.text-content>h1{
    font-size: 5vh;
    font-weight: 700;
    margin: 10% 10% 6% 10%;
    color: rgb(174, 23, 239);
}
.text-content>p{
    text-align: justify;
    font-size: 2.5vh;
    color: rgb(27, 27, 28);
    font-weight: 600;
}
.ctas{
    width: 50%;
    display: flex;
    margin: 0% 0% 0% 0%;
}
.banner-cta{
    /* height: 4vh; */
    /* width: 20vw; */
    font-size: 2.5vh;
    text-align: center;
    align-items: center;
    margin: 6% 0% 0% 50%;
    border-radius: 10px;
    background-color: transparent;
    border: 2px solid rgb(229, 120, 231);
}
.banner-cta:hover{
    background-color: rgb(156, 16, 159);
    border: none;
    color: white;
}
.showup-img{
    height: 80vh;
    /* margin-left: 60%; */
    /* border: 1px solid black; */
}

.text-center{
    color: rgb(174, 23, 239);
    font-weight: 700;
}
.main-div{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
}
.cart-div{
    margin: 2% 0% 2% 2%;
    border-radius: 13px;
    box-shadow: 1px 1px 10px rgb(78, 77, 77);
    background-color: whitesmoke !important;
}
.card{
    border-radius: 13px;
    margin: 2% 0% 2% 2%;
    border: none;
    /* border-radius: 10px; */
    /* box-shadow: 2px 2px 2px rgb(78, 75, 75); */
    background-color: whitesmoke !important;
}

.footer-dark {
	position: absolute; 
    padding: 50px 0;
    color: white;
    /* color:#f0f9ff; */
    background-color: black;
    /* background-color:#282d32; */
}
.container5{
	margin: 0% 7% 0% 7%;
}

.footer-dark h3 {
    margin-top: 0;
    margin-bottom: 12px;
    font-weight: bold;
    font-size: 16px;
    color: #FF0063;
}

.footer-dark ul {
    padding: 0;
    list-style: none;
    line-height: 1.6;
    font-size: 14px;
    margin-bottom: 0;
}

.footer-dark ul .af {
    color: inherit;
    text-decoration: none;
    opacity: 1;
}

.footer-dark ul .af:hover {
    opacity: 0.8;
}

@media (max-width:767px) {
    .footer-dark .item:not(.social) {
        text-align: center;
        padding-bottom: 20px;
    }
}

.footer-dark .item.text {
    margin-bottom: 36px;
}

@media (max-width:767px) {
    .footer-dark .item.text {
        margin-bottom: 0;
    }
}

.footer-dark .item.text p {
    opacity: 0.6;
    margin-bottom: 0;
}

.footer-dark .item.social {
    text-align: center;
}

@media (max-width:991px) {
    .footer-dark .item.social {
        text-align: center;
        margin-top: 20px;
    }
}

.footer-dark .item.social>.af {
    font-size: 20px;
    width: 36px;
    height: 36px;
    line-height: 36px;
    display: inline-block;
    text-align: center;
    border-radius: 50%;
    box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.4);
    margin: 0 8px;
    color: #fff;
    opacity: 0.75;
}

.footer-dark .item.social>.af:hover {
    opacity: 0.9;
}

.footer-dark .copyright {
    text-align: center;
    padding-top: 24px;
    opacity: 0.3;
    font-size: 13px;
    margin-bottom: 0;
}
